.cart-btn {
    padding: 8px;
    font-size: .85rem;
    max-width: 120px;
}

.btn {
    border-radius: var(--radius);
    background-color: #f54748;
    color: #fff;
    border: none;
    cursor: pointer;
    letter-spacing: var(--spacing);
    display: inline-block;
    box-shadow: 0px 0px 2px 1px rgba(0, 0, 0, .52);
    font-family: inherit;
    text-transform: capitalize;
    transition: var(--transition);
}

.btn:hover {
    color: #000;
    background: transparent;
}

.btn:hover .item-cnt {
    color: #fff;
    background: #f54748;
}

.btn:active {
    transform: scale(.9);
}

.category-btn {
    padding: 7px;
    margin-right: 20px;
}